// @flow
import React, { Fragment } from 'react';
import _isEmpty from 'lodash/isEmpty';
import { Loader } from 'react-loaders';
import BlockUi from 'react-block-ui';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import type { Element } from 'react';
import Property from './Property';
import usePropertiesStatuses from './usePropertiesStatuses';
import { Widget } from 'components/Widget';
import type { Property as PropertyType } from 'types/Property';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
    marginBottom: theme.spacing(3),
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
  widget: {
    marginBottom: theme.spacing(3),
  },
}));

type Props = {
  data: Array<PropertyType>,
  isLoadingData: Boolean,
};

const PropertyList = ({
  data: properties,
  isLoadingData,
}: Props): Element<'div' | typeof Fragment> => {
  const { steps, loadingStatuses } = usePropertiesStatuses();
  const classes = useStyles();
  const isLoading = isLoadingData || loadingStatuses;

  if (_isEmpty(properties) && !isLoading) {
    return <></>;
  }

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, {
        [classes.loading]: isLoading,
      })}
    >
      <Widget className={classes.widget} title="Home Sale Status">
        {properties.map(property => (
          <Property
            property={property}
            steps={steps}
            key={property.PropertyId}
          />
        ))}
      </Widget>
    </BlockUi>
  );
};

export default PropertyList;
