// @flow
import { useEffect } from 'react';
import { useQuery } from '@apollo/client';
import { LIST_PROPERTY_STATUSES } from 'graphql/queries/properties/status';
import _get from 'lodash/get';
import { PROPERTY_ERROR_MESSAGE } from 'shared/Messages';
import { toastr } from 'react-redux-toastr';

const STEP_LIMIT = 7;

const usePropertiesStatuses = (): {
  steps: string[],
  loadingStatuses: boolean,
} => {
  const {
    loading: loadingStatuses,
    error: errorStatuses,
    data: statusesData,
  } = useQuery(LIST_PROPERTY_STATUSES);
  const statuses = _get(statusesData, 'listPropertyStatuses', []);

  useEffect(() => {
    const { message: statusesMessage } = errorStatuses || {};
    if (statusesMessage) {
      toastr.error(PROPERTY_ERROR_MESSAGE.fetchFailure, statusesMessage);
    }
  }, [errorStatuses]);

  return {
    steps: statuses.slice(1, STEP_LIMIT).map(status => _get(status, 'Name')),
    loadingStatuses,
  };
};

export default usePropertiesStatuses;
